.CG2-buttonContainer {
  margin: 20px 0;
  ul {
    font-size: 0;
    list-style: none;
    text-align: center;
    padding: 0;
    margin: 0;
  }
  &.CG2-buttonContainer--pullLeft ul {
    text-align: left;
  }
  li {
    font-size: 1rem;
    display: inline-block;
    margin: 0 8px;
    &.CG2-buttonContainer__item--full {
      display: block;
      margin: 0 0 8px;
    }
    @include max-screen( $breakpoint-middle ) {
      display: block;
    }
  }
  &.CG2-buttonContainer--pullLeft li {
    margin: 0 16px 0 0;
  }
  @include max-screen( $breakpoint-middle ) {
    li,
    &.CG2-buttonContainer--pullLeft li {
      margin: 0 0 5px;
    }
  }
  .CG2-button {
    @include max-screen( $breakpoint-middle ) {
      font-size: 16px;
      text-align: center;
      padding: .4em 10px;
      width: 100%;
    }
  }
}


