---

```html
<div class="CG2-paymentTable">
  <table>
    <thead>
      <tr>
        <th colspan="2"><img src="https://www.codegrid.net/img/payment/img_01.svg" alt="VISA, MasterCard, JCB, American Express, Diners Club" width="275"></th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <th><br><span class="CG2-paymentTable__annotation"></span></th>
        <td>
          <div class="CG2-paymentTable__priceList">
            <ul>
              <li>
                <div class="CG2-paymentTable__priceLabel"></div>
                <div class="CG2-paymentTable__priceValue">800</div>
                <div class="CG2-paymentTable__priceUnit"></div>
                <div class="CG2-paymentTable__priceAnnotation">864</div>
              </li>
            </ul>
          </div>
        </td>
      </tr>
      <tr>
        <th></th>
        <td></td>
      </tr>
      <tr>
        <th></th>
        <td>
          <div class="CG2-paymentTable__valueList">
            <ul>
              <li>
                <div class="CG2-paymentTable__valueText CG2--disableLargeScreen"></div>
                <div class="CG2-paymentTable__value">30</div>
                <div class="CG2-paymentTable__valueUnit"></div>
              </li>
            </ul>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
</div>
```
*/
.CG2-paymentTable {
  table {
    margin: 40px auto;

    @include max-screen( $breakpoint-small ) {
      table-layout: fixed;
      margin-top: 20px;
      margin-bottom: 30px;
    }
  }

  th,
  td {
    font-weight: normal;
    vertical-align: middle;
    padding: 15px 40px;
    border: 1px solid #dbdbdb;
    @include max-screen( $breakpoint-small ) {
      vertical-align: top;
      width: 50%;
      padding: 20px 10px;
    }
  }

  thead th,
  thead td {
    border: none;
  }

  tbody th {}
  tbody td {}

  th:first-child,
  td:first-child {
    @include max-screen( $breakpoint-small ) {
      display: none;
    }
  }
}

.CG2-paymentTable__annotation {
  font-size: 12px;
}

.CG2-paymentTable__priceList {
  text-align: left;
  display: inline-block;

  ul {
    border-collapse: separate;
    border-spacing: 30px 0;
    display: table;
    line-height: normal;
    padding: 0;
    margin: 0 -30px;

    @include max-screen( $breakpoint-small ) {
      display: block;
      margin: 0;
    }
  }

  li {
    display: table-cell;
    margin: 0;

    @include max-screen( $breakpoint-small ) {
      display: block;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.CG2-paymentTable__priceLabel {
  font-size: 12px;
}

.CG2-paymentTable__priceValue {
  font-size: 32px;
  font-family: $fontCode;
  line-height: 1;
  vertical-align: bottom;
  display: inline-block;

  @include max-screen( $breakpoint-small ) {
    font-size: 28px;
  }
}

.CG2-paymentTable__priceUnit {
  font-size: 16px;
  vertical-align: bottom;
  display: inline-block;
}

.CG2-paymentTable__priceAnnotation {
  font-size: 12px;
}

.CG2-paymentTable__valueList {
  text-align: left;
  display: inline-block;

  ul {
    border-collapse: separate;
    border-spacing: 30px 0;
    display: table;
    line-height: normal;
    padding: 0;
    margin: 0 -30px;

    @include max-screen( $breakpoint-small ) {
      display: block;
      margin: 0;
    }
  }

  li {
    display: table-cell;
    margin: 0;
    
    @include max-screen( $breakpoint-small ) {
      display: block;
    }
  }
}

.CG2-paymentTable__valueText {
  display: inline-block;

  @include max-screen( $breakpoint-small ) {
  }
}

.CG2-paymentTable__value {
  font-size: 32px;
  font-family: $fontCode;
  line-height: 1;
  vertical-align: bottom;
  display: inline-block;

  @include max-screen( $breakpoint-small ) {
    font-family: $fontSansSerif;
    font-size: 100%;
    line-height: inherit;
  }
}

.CG2-paymentTable__valueUnit {
  font-size: 16px;
  vertical-align: bottom;
  display: inline-block;

  @include max-screen( $breakpoint-small ) {
    font-size: 100%;
  }
}


